.breed-select {
  margin-bottom: 20px;
}

.cat-info {
  gap: 20px;
  display: flex;
}

.temper {
  font-weight: 700;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #bd08ce;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #000;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

.custom-select-container {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  display: inline-block;
  position: relative;
}

